import React from 'react';
import { Redirect } from 'react-router-dom';

import RootMainLayout from './RootMainLayout';
import PageLayout from './PageLayout';

class UserMainLayout extends React.Component {
  state = {
    authenticated: this.props.authenticated,
    user: this.props.user,
    logout: this.props.logout,
  };

  onCollapse = (collapsed) => {
    console.log(collapsed);
    this.setState({ collapsed });
    console.log(this.state.user);
  };

  render() {
    const { authenticated, user, logout } = this.state;
    return authenticated === false ? (
      <Redirect to='/login' />
    ) : user.email === 'root@travelnote.com' ? (
      <RootMainLayout logout={logout} />
    ) : (
      <PageLayout logout={logout} />
    );
  }
}

export default UserMainLayout;
